.list {
    padding-left: 0;
}

.list__inline {
    margin-bottom: 15px;
    .list__item {
        display: inline-block;
        margin-left: 5px;
        margin-right: 5px;
        border: 1px solid $black;
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }
    .list__link {
        color: $black;
        text-decoration: none;
    }

    .list__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
    }
}